body {
  margin: 0;
  padding: 0;
  font-family: Helvetica, sans-serif;
}

* {
  font-family: Helvetica, sans-serif;
}

.App {
  width: 100%;
  min-height: 100vh;

  display: -ms-flexbox;

  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;

  background-image: url('/img/bg.jpg');
  background-size: cover;
}
.white-wrapper {
  background-color: rgba(250, 250, 250, 0.92);
  margin-top: 10vh;
  margin-bottom: 7.4vh;
  padding: 40px;
  border-radius: 20px;
  width: 100%;
  max-width: 500px;
}

/* FLEX */
.flex {
  display: -ms-flexbox;
  display: flex;
}
.flex-center {
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;

  font-size: 0.9rem;
}
/* END FLEX */

.Title {
  font-size: 1.5rem;
  padding: 30px 0;
  text-align: center;
}

.form-field {
  padding: 10px 0;

  display: -ms-flexbox;

  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;

  -ms-flex-direction: column;

      flex-direction: column;
}
.form-field > label {
  display: block;
  text-align: center;
  font-size: 1.05rem;
}
.form-field > input, .form-field > textarea {
  min-width: 150px;
  width: 92%;
  max-width: 476px;

  margin: 15px 0;
  border: 1px solid #aaa;
  border-radius: 10px;
  padding: 8px 12px;
  font-size: 1rem;
}

.btn {
  border: 1px solid #aaa;
  margin: 10px 12px;
  padding: 10px 15px;
  border-radius: 10px;
  font-size: 1.1rem;
  cursor: pointer;
  width: 150px;
}
.btn-done {
  border: 1px solid rgb(50, 118, 178);
  margin: 10px auto;
  text-align: center;
  background-color: rgb(22, 119, 180);
  color: #fefefe;
}
.btn-done:hover {
  margin: 10px auto;
  background-color: rgb(17, 14, 108);
  color: #fefefe;
}

.link-result {
  min-width: 150px;
  max-width: 440px;

  background-color: #cecece;
  padding: 20px 30px;
  margin: 10px auto;
  border-radius: 10px;
  border: 1px solid #aaa;  

  word-wrap: break-word;

  text-align: center;
}
.result { 
  border-width: 0;
  resize: none;
  width: calc(100%);
  height: 100px;
  background-color: transparent;
  font-size: 1.1rem;
  padding: 10px 0;
}


